import React from "react";
import one from "./1.jpg";
import two from "./2.jpg";
import three from "./3.jpg";
import four from "./4.jpg";
import five from "./5.jpg";
import ViewPicture from "./ViewPicture.js";
import M from "materialize-css";
import { DEFAULT_ALBUM } from './App.js';

const DEFAULT_LENGTH = 5;

let carouselOptions = {
  duration: 300,
  dist: 50,
  numVisible: DEFAULT_LENGTH,
};

const modalOptions = {
  opacity: 0,
};

class PhotoCarousel extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      picture: one,
      caption: "",
      date: "",
      showModal: false,
      modal: null,
    };
  }

  componentDidUpdate(prevProps) {
    if (this.props !== prevProps && this.props.photos.length !== 0) {
      let elem = document.querySelector(".carousel");
      carouselOptions.numVisible = this.props.photos.length;
      M.Carousel.init(elem, carouselOptions);
    }

    if (this.props.photos.length === 0) {
      //console.log("Empty album!");
      let elem = document.querySelector(".carousel");
      carouselOptions.numVisible = DEFAULT_LENGTH;
      M.Carousel.init(elem, carouselOptions);
    }
  }

  componentDidMount() {
    let elem = document.querySelector(".carousel");
    M.Carousel.init(elem, carouselOptions);
    let elem2 = document.getElementById("modal1");
    let modal = M.Modal.init(elem2, modalOptions);
    this.setState({ modal: modal });
  }

  handleClick = (t) => {
    const pic = t.target.attributes.alt.value;
    const caption = t.target.attributes[0].value;
    const date = t.target.attributes[1].value;

    this.setState({
      picture: pic,
      caption: caption,
      date: date,
    });

    this.state.modal.open();
  }

  pushDefault = (arr, picture, alt, caption, date) => {
    arr.push({
      picture: picture,
      alt: alt,
      caption: caption,
      date: date,
    });
  }

  deleteCallBack = () => {
	this.state.modal.close();
	this.props.setAlbum(this.props.album);
  }

  render() {
    let arr = [];
    const captions = [
      "Victorian Belle",
      "Peacock Lane",
      "Zoolights",
      "Fire on the Mountain",
      "Steven's House",
    ];
    const date = ["2018", "2018", "2018", "2019", "2020"];

    if (this.props.album === "default") {
      this.pushDefault(arr, five, five, captions[4], date[4]);
      this.pushDefault(arr, one, one, captions[0], date[0]);
      this.pushDefault(arr, two, two, captions[1], date[1]);
      this.pushDefault(arr, three, three, captions[2], date[2]);
      this.pushDefault(arr, four, four, captions[3], date[3]);
    }

    return (
      <div style={{ height: "100%" }}>
        <ViewPicture
          current={this.state.picture}
          caption={this.state.caption}
          date={this.state.date}
		  album={this.props.album}
		  deleteCallBack={this.deleteCallBack}
		  token={this.props.idtoken}
        />
        <h2 style={{ marginBottom: "-80px" }}>
		  {
		    (this.props.album === DEFAULT_ALBUM || this.props.album === undefined) ? "Album 1" : this.props.album + " Album"
		  }
		</h2>
        <div className="carousel" style={{ height: "500px" }}>
          {this.props.photos.map((item) => (
            <a className="carousel-item" href="#!">
              <img
                data-caption={item.caption}
                data-date={item.date}
                alt={item.link}
                src={item.link}
                onClick={this.handleClick}
              />
            </a>
          ))}
          {arr.map((item) => (
            <a className="carousel-item" href="#!" key={item.picture}>
              <img
                data-caption={item.caption}
                data-date={item.date}
                alt={item.alt}
                src={item.picture}
                onClick={this.handleClick}
              />
            </a>
          ))}
        </div>
      </div>
    );
  }
}

export default PhotoCarousel;

